.main-menu {
  width: 100%; }
  .main-menu ul {
    display: block;
    margin: 0;
    padding: 0; }
  .main-menu .menu-item {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    height: 60px;
    align-items: center;
    font-size: 1.5em;
    color: #FFECDB;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    background: #34495E;
    transition: all ease-in-out 0.4s;
    position: relative; }
    .main-menu .menu-item a {
      color: #FFECDB; }
      .main-menu .menu-item a:hover:after {
        border-bottom: 1px solid #FFECDB; }
    .main-menu .menu-item:hover {
      background: #3b536b; }
    .main-menu .menu-item.active {
      background: #cbb6a1; }
      .main-menu .menu-item.active:after {
        font-family: Ionicons;
        content: '\f361';
        color: #F2F1EF;
        position: absolute;
        right: 0; }

.mm-tabs {
  background: #fff;
  display: block;
  width: 100%;
  border: 1px solid rgba(0, 0, 0, 0.07); }

.mm-tabs__menu {
  height: 50px;
  display: flex;
  background: rgba(0, 0, 0, 0.04);
  text-transform: uppercase;
  letter-spacing: 1px; }

.mm-tab {
  display: flex;
  min-width: 100px;
  padding: 0 20px;
  height: inherit;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #34495E; }
  .mm-tab i {
    font-size: 1.4em; }
  .mm-tab:last-of-type {
    border: none; }
  .mm-tab:hover {
    background: rgba(0, 0, 0, 0.02); }

.mm-tab--active {
  background: #fff;
  font-weight: 700; }
  .mm-tab--active:hover {
    background: #fff; }

.mm-tabs__content {
  padding: 20px;
  display: none;
  min-height: 100px; }
  .mm-tabs__content > .mm-row {
    padding: 0; }

.mm-tabs__content--active {
  display: block; }

/* Responsive */
@media only screen and (max-width: 800px) {
  .mm-tabs__menu {
    display: block;
    height: auto; }
  .mm-tab {
    width: 100%;
    height: 50px; } }

.mm-accordion {
  width: 100%; }

.mm-accordion--indicator .mm-accordion__header:after {
  font-family: Ionicons;
  content: '\f401';
  position: absolute;
  padding: 2px;
  right: 20px; }

.mm-accordion--indicator .mm-accorion__header--active:after {
  content: '\f400'; }

.mm-accordion__item {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.07);
  margin-top: -2px;
  position: relative; }
  .mm-accordion__item::first-of-type {
    margin-top: 0; }

.mm-accordion__header {
  height: 60px;
  display: flex;
  align-items: center;
  padding: 0 20px;
  cursor: pointer;
  position: relative; }
  .mm-accordion__header:hover {
    background: rgba(0, 0, 0, 0.03); }

.mm-accorion__header--active {
  background: rgba(0, 0, 0, 0.03); }

.mm-accordion__content {
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  padding: 10px;
  display: none;
  border-top: 1px solid rgba(0, 0, 0, 0.07); }

