.mm-accordion-11 {
  width: calc(100 / 12 * 11)%; }

.mm-accordion-12 {
  width: calc(100 / 12 * 12)%; }

.mm-offcanvas {
  width: 300px;
  background: #2a2a2a;
  padding: 0;
  margin: 0;
  position: fixed;
  z-index: 100;
  height: 100%;
  right: 0;
  color: #fff;
  right: -300px;
  -webkit-transition: 200ms linear all;
  transition: 200ms linear all;
  overflow-y: scroll; }

.mm-offcanvas--open {
  right: 0;
  -webkit-transition: 200ms linear all;
  transition: 200ms linear all; }

.mm-offcanvas--active {
  -webkit-transform: translateX(-300px);
  transform: translateX(-300px);
  transform: translate3d(-300px, 0, 0);
  -webkit-transform: translate3d(-300px, 0, 0); }

.mm-offcanvas__header {
  height: 60px;
  padding: 0 20px;
  display: flex;
  align-items: center;
  background: #252525;
  text-transform: uppercase;
  font-weight: 700;
  position: relative; }

.mm-offcanvas__menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: none; }

.mm-offcanvas__menu-item {
  height: 40px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
  padding: 0 20px;
  text-transform: uppercase;
  font-size: 0.8em;
  position: relative; }
  .mm-offcanvas__menu-item a {
    color: #fff; }
  .mm-offcanvas__menu-item:hover {
    background: #252525; }
  .mm-offcanvas__menu-item:before {
    font-family: Ionicons;
    content: '\f3a7';
    margin-right: 10px;
    font-size: 1.1em;
    color: rgba(255, 255, 255, 0.1); }

.mm-offcanvas__menu-item--active:before {
  color: rgba(29, 185, 84, 0.8); }

.mm-offcavas__close {
  position: absolute;
  right: 0;
  color: #E84C3D;
  cursor: pointer;
  display: none;
  padding: 20px;
  align-items: center;
  justify-content: center;
  height: 60px;
  padding-top: 20px;
  top: 0; }
  .mm-offcavas__close:hover {
    background: #272727; }

.mm-offcanvas-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
  display: none; }

.mm-modal {
  display: none;
  position: absolute;
  z-index: 101;
  margin: auto auto;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%); }

.mm-modal-overlay {
  width: 100%;
  height: 100%;
  position: fixed;
  background: rgba(0, 0, 0, 0.5);
  z-index: 100;
  display: none; }
