@import "config/mimi_config";

.mm-message {
  text-align: left;
  width: 100%;
  background: #fff;
  border-radius: $default_border_radius;
  position: relative;
  box-sizing: border-box;
  padding: 10px;
  border: $default_border;
  font-weight: 200;
}

.mm-message__header{
  display: flex;
  align-items: center;
  justify-content: left;
  text-transform: uppercase;
  font-size: 1.2em;
  margin-bottom: 10px;
  font-weight: 700;
}

.mm-message__text {
  margin-bottom: 0;
}

// .mm-message--round {
//   .mm-message-main {
//     border-radius: 200em;
//   }
// }

$list: info warning success error primary dark;
@each $color in $list {
  .mm-message--#{$color} {
    background: transparentize(map-get($colors, $color), 0.1);
    color: $white;
  }
}


@for $i from 1 through $default_col_number {
  .mm-message-#{$i} {
    width: calc(100 / #{$default_col_number} * #{$i}) + %;
  }
}